.partner-content{
    background-color: #EC407A05;
    margin-top: 150px;
    width: 100%;
    >div{
        max-width: 1140px;
        margin: auto;
        display: flex;
        justify-content: space-between;
        @media (max-width:768px) {
            display: block;
        }
        >div{
            width: 489px;
            @media (max-width:768px) {
                width: 100vw;
            }
        }
        >:first-child{
            display: none;
            @media (max-width:768px) {
                display: block;
                text-align: center;
                img{
                    margin-top: 72px;
                    width: 240px;
                }
            }
        }
        >:nth-child(2){
            @media (max-width:768px) {
                padding: 0 15px 70px;
            }
            >:first-child{
                margin-top: 120px;
                font-size: 40px;
                font-weight: 700;
                width: 470px;
                @media (max-width:768px) {
                    width: 80%;
                    font-size: 20px;
                    margin-top: 40px;
                }
            }
            >:nth-child(2),>:nth-child(3),>:nth-child(4){
                font-size: 14px;
                line-height: 25px;
            }
            >:nth-child(2),>:nth-child(3){
                margin-top:25px ;
            }
            >:nth-child(3),>:nth-child(4){
                >span,a{
                    color: #EC407A;
                    font-weight: bold;
                }
            }
            >:last-child{
                margin-top: 32px;
                margin-bottom: 125px;
                @media (max-width:768px) {
                    margin-bottom:0px ;
                }
                img{
                    margin-right: 32px;
                    cursor: pointer;
                    &:hover{
                        opacity: 0.8;
                    }
                }
            }
        }
        >:nth-child(3){
            @media (max-width:768px) {
                display: none;
            }
            margin-top: 270px;
            img{
                width: 400px;
            }
        }
    }
}